
.atmInfo__card_image_info {
	position: relative;
	height: 237px;
	padding: 15px 12px 12px 12px;
	margin: 16px 0;
}

.atmInfo__card_image {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}

.atmInfo__card_details {
	position: relative;
	display: flex;
	flex-direction: column;
	height: 100%;
}

.atmInfo__action {
	background-color: #fff;
	border-radius: 4px;
	display: flex;
	align-items: center;
	padding: 22px 16px 22px 32px;
	margin-top: 16px;
}

.pointer {
	cursor: pointer;
}

.wallet {
	height: 26.74px;
	max-width: 167px;
}

.atmInfo__action_in_list {
	border-radius: 4px;
	border-bottom: 1px solid var(--border-color-1);
	display: flex;
	align-items: center;
	padding: 22px 16px 22px 32px;
}